'use client';

import React from 'react';
import { Order } from '@/lib/orders';

interface OrderStatusProps {
  order: Order & {
    estimatedDeliveryFormatted: string;
  };
}

const OrderStatus: React.FC<OrderStatusProps> = ({ order }) => {
  // Determine progress percentage based on status
  const getProgress = (status: string) => {
    const statusMap = {
      'pending': 15,
      'in_progress': 30,
      'stringing': 60,
      'quality_check': 85,
      'completed': 95,
      'ready_for_pickup': 100
    };
    return statusMap[status as keyof typeof statusMap] || 0;
  };

  // Get status display text
  const getStatusText = (status: string) => {
    const statusTextMap = {
      'pending': 'Order Received',
      'in_progress': 'Preparing Racket',
      'stringing': 'Stringing in Progress',
      'quality_check': 'Quality Check',
      'completed': 'Stringing Complete',
      'ready_for_pickup': 'Ready for Pickup'
    };
    return statusTextMap[status as keyof typeof statusTextMap] || status;
  };

  // Get progress bar color based on status
  const getProgressColor = (status: string) => {
    if (status === 'ready_for_pickup' || status === 'completed') {
      return 'bg-green-500';
    } else if (status === 'stringing' || status === 'quality_check') {
      return 'bg-blue-500';
    } else {
      return 'bg-yellow-500';
    }
  };

  const progress = getProgress(order.status);
  const statusText = getStatusText(order.status);
  const progressColor = getProgressColor(order.status);

  return (
    <div className="max-w-md mx-auto bg-white rounded-lg shadow-lg p-6 border border-gray-200">
      {/* Header */}
      <div className="text-center mb-6">
        <h1 className="text-2xl font-bold text-gray-900 mb-2">Order Status</h1>
        <p className="text-sm text-gray-600">Order ID: {order.id}</p>
      </div>

      {/* Progress Bar */}
      <div className="mb-6">
        <div className="flex justify-between items-center mb-2">
          <span className="text-sm font-medium text-gray-700">Progress</span>
          <span className="text-sm font-medium text-gray-700">{progress}%</span>
        </div>
        <div className="w-full bg-gray-200 rounded-full h-3">
          <div
            className={`h-3 rounded-full transition-all duration-500 ease-out ${progressColor}`}
            style={{ width: `${progress}%` }}
          ></div>
        </div>
        <p className="text-sm text-gray-600 mt-2 text-center font-medium">{statusText}</p>
      </div>

      {/* Order Details */}
      <div className="bg-gray-50 rounded-lg p-4 mb-6">
        <h3 className="text-lg font-semibold text-gray-900 mb-3">Order Details</h3>
        <div className="space-y-2">
          <div className="flex justify-between">
            <span className="text-sm font-medium text-gray-600">Racket:</span>
            <span className="text-sm text-gray-900 text-right">{order.racket}</span>
          </div>
          <div className="flex justify-between">
            <span className="text-sm font-medium text-gray-600">String:</span>
            <span className="text-sm text-gray-900 text-right">{order.string}</span>
          </div>
          <div className="flex justify-between">
            <span className="text-sm font-medium text-gray-600">Tension:</span>
            <span className="text-sm text-gray-900 text-right">{order.tension}</span>
          </div>
          <div className="flex justify-between">
            <span className="text-sm font-medium text-gray-600">Status:</span>
            <span className="text-sm text-gray-900 text-right capitalize">{statusText}</span>
          </div>
          {order.notes && (
            <div className="pt-2 border-t border-gray-200">
              <span className="text-sm font-medium text-gray-600">Notes:</span>
              <p className="text-sm text-gray-900 mt-1">{order.notes}</p>
            </div>
          )}
        </div>
      </div>

      {/* Estimated Delivery */}
      <div className="bg-blue-50 rounded-lg p-4 mb-6">
        <h3 className="text-lg font-semibold text-gray-900 mb-2">Estimated Delivery</h3>
        <p className="text-lg font-medium text-blue-700">{order.estimatedDeliveryFormatted}</p>
        {order.status === 'ready_for_pickup' && (
          <p className="text-sm text-green-600 mt-1 font-medium">Ready for pickup now!</p>
        )}
      </div>

      {/* Contact Button */}
      <button
        className="w-full py-3 px-4 bg-black text-white font-medium rounded-lg hover:bg-gray-800 transition-colors duration-200 focus:outline-none focus:ring-2 focus:ring-gray-500 focus:ring-offset-2"
        onClick={() => {
          // In a real app, this could open a modal, navigate to contact page, or initiate a call
          const message = `Hi, I have a question about my order ${order.id}. Current status: ${statusText}`;
          const encodedMessage = encodeURIComponent(message);
          window.open(`mailto:support@racketstringing.com?subject=Order Inquiry - ${order.id}&body=${encodedMessage}`, '_blank');
        }}
      >
        Contact Support
      </button>

      {/* Additional Actions */}
      <div className="mt-4 text-center">
        <p className="text-xs text-gray-500">
          Questions? Call us at{' '}
          <a href="tel:+1-555-0123" className="text-blue-600 hover:underline">
            (555) 012-3456
          </a>
        </p>
      </div>
    </div>
  );
};

export default OrderStatus;
